import ErmjBaseHandler from "./ErmjBaseHandler";

export default class ErmjBankerHandler extends ErmjBaseHandler{
    protected execute(msg){
        let banker = msg._para.banker;
        let bankerLocalSeat = this.SitHelper.serverSToLocalN(banker);
        this.context.set(this.Define.FieldBankerSeat, bankerLocalSeat);
        this.mainUI.onBanker(bankerLocalSeat, msg._para.dice, true);
    }

    protected executeSync(msg){
        let banker = msg._para.banker;
        let bankerLocalSeat = this.SitHelper.serverSToLocalN(banker);
        this.context.set(this.Define.FieldBankerSeat, bankerLocalSeat);

        this.mainUI.onBanker(bankerLocalSeat, msg._para.dice, false);
    }
}